export const query = graphql`
  query {
    allMdx(
      sort: { fields: frontmatter___date, order: DESC }
      filter: { frontmatter: { publish: { eq: 1 } } }
    ) {
      nodes {
        frontmatter {
          date(formatString: "MMMM YYYY")
          title
        }
        id
        slug
      }
    }
  }
`;
